import * as React from "react"
import { Link, graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import Layout from "../components/layout"
import Seo from "../components/seo"
import TagsList from "../components/tagsList"

const BlogPostTemplate = ({
  data: { previous, next, site, markdownRemark: post },
  location,
}) => {
  const siteTitle = site.siteMetadata?.title || `Title`;
  let featuredImg = getImage(post.frontmatter.featuredImage?.childImageSharp?.gatsbyImageData);

  return (
    <Layout location={location} title={siteTitle} language={post.fields.language}>
      <article
        className="blog-post"
        itemScope
        itemType="http://schema.org/Article"
      >
        <header>
          <div className="back-to-list"><Link to={`/${post.fields.language}/${post.fields.contentType}/`}>[{post.fields.contentType}]</Link></div>
          <h1 itemProp="headline">{post.frontmatter.title}</h1>
          <div className="article-byline">{post.frontmatter.date} <TagsList tags={post.frontmatter.tags} barBefore /></div>
        </header>
        <section id="extranotes">
          {!!post.frontmatter.description && <p>{post.frontmatter.description}</p>}
          {!!post.frontmatter.leadText && <p>{post.frontmatter.leadText}</p>}
          {!!post.frontmatter.notesText && <p>{post.frontmatter.notesText}</p>}
          {!!post.frontmatter.authorName && <p>by {post.frontmatter.authorName}</p>}
        </section>
        <section><GatsbyImage image={featuredImg} /></section>
        <section
          dangerouslySetInnerHTML={{ __html: post.html }}
          itemProp="articleBody"
        />
        <hr />
        <footer>
          <Link to="/"><img src={`${__PATH_PREFIX__}/img/logo-inverse.svg`} width="100" /></Link>
        </footer>
      </article>
    </Layout>
  )
}

export const Head = ({ data: { markdownRemark: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
    />
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      fields {
        language
        contentType
      }
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
        authorName
        leadText
        notesText
        featuredImage {
          childImageSharp {
            gatsbyImageData(width: 800, height: 411)
          }
        }
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
